import { useState, useRef, useEffect } from "react";
import cloud_bad from "../img/cloud_bad.png";
import cloud_good from "../img/cloud_good.png";
import cloud_blue from "../img/cloud_blue.png";
import cloud from "../img/cloud.png";

const Word = ({ children, top, left, right }) => {
    const [isActive, setActive] = useState(false);
    const [actualStyle, setStyle] = useState(`url(${cloud})`);
    const ref = useRef();

    useEffect(() => {
        if (ref.current.classList.contains("word-container-active")) {
            setStyle(`url(${cloud_blue})`);
        } else if (ref.current.classList.contains("word-container-bad")) {
            setStyle(`url(${cloud_bad})`);
        } else if (ref.current.classList.contains("word-container-good")) {
            setStyle(`url(${cloud_good})`);
        } else {
            setStyle(`url(${cloud})`);
        }
    }, [isActive]);

    return (
        <div
            className={
                isActive
                    ? "word-container word-container-active"
                    : "word-container"
            }
            style={{
                top: top,
                left: left,
                right: right,
                backgroundImage: actualStyle,
            }}
            onClick={() => setActive(!isActive)}
            ref={ref}
        >
            <div className="word">{children}</div>
        </div>
    );
};

export default Word;
